/* https://colorhunt.co/palette/2414689f0d7fea1179f79bd3 */

html{
  /* width: 100vw;
  height: 100vh; */
  margin: 0;
  padding: 0;

}

:root {
  /* *************** */
  /* タイトル・リンク */
  /* *************** */
  /* --headder-height      : 9.9vh; */
  --headder-height      : 10vh;
  --headder-left-width  : 70vw;
  --headder-right-width : 30vw;

  /* ******************** */
  /* 操作・設定・コード画面 */
  /* ******************** */
  --main-height         : 85vh;

  --main-left-width     : 50vw;
  --main-left-contents  : 60%;

  --main-right-width    : 50vw;

  /* ******************** */
  /*         末尾         */
  /* ******************** */
  --fotter-height       : 3vh;
  --fotter-top       : 94.2vh;
}

body{
  margin: 0;
}

#root{
  width:  100vw;
  height: 100vh;
}

.App {
  width:  100vw;
  height: 100vh;
  font-family: sans-serif;
  /* text-align: center; */
  /* background: #241468; */
  position: relative;
}

#Background{
  width: 100vw;
  height: 100vh;
  /* position: absolute;
  top: 0;
  left: 0; */
}

#Contents {
  position: absolute;
  top: 0;
  left: 0;
}

/* *************** */
/* headerコンテンツ */
/* *************** */
header{
  display: block flex;  /* .titleと.hyperlinkの並列 */
  margin: 0;
  width: 100vw;
  height: var(--headder-height);
  /* background: rgb(255, 255, 0); */
}

header>.title{
  width: var(--headder-left-width);
}

header>.title>h1{
  width: 100%;
  margin: 0;
  margin-left: 5%;
}

header>.hyperlink{
  width: var(--headder-right-width);
}

header>.hyperlink>ul{
  margin: 0;
}

/* ************* */
/* mainコンテンツ */
/* ************* */
main{
  display: block flex;  /* .Operationと.mainRightContentの並列 */
  width: 100vw;
  height: var(--main-height);

  /* flexアイテム同士の余白 */
  /* justify-content: space-between; */
}

main>.Operation {
  width: var(--main-left-width);
  height: 100%;

  /* 子要素を上下中央寄せ */
  /* display: flex;
  align-items: center;
  justify-content: center; */

  /* background: rgb(255, 0, 0); */
}

main>.Operation>.OperationContents{
  width: var(--main-left-contents);
  margin-left: auto;

  /* margin: auto; */
  
  /* text-align: center; */
}

main>.Operation>.OperationContents>.OperationButton{
  width: 100%;
  display: block flex;  /* .titleと.hyperlinkの並列 */
}

/* ボタン */
main button{
  height: 5vh;
  /* margin: 1vh; */
  /* background: rgb(0, 145, 48); */
}

main .leftButton{
  display: block;
  margin-right: auto;
  /* text-align: left; */
}

main .rightButton{
  display: block;
  margin-left: auto;
}

/* main>.Operation>#Canvas{
  text-align: center;
} */

main>.mainRightContent{
  width: var(--main-right-width);
  height: 100%;
  /* background: #EA1179; */
  /* background: yellow; */
}

main>.mainRightContent>.Option{
  width: 100%;
  height: 45%;

  /* display: flex;
  align-items: center;
  justify-content: center; */

  /* background: #EA1179; */
  /* background: yellow;  */
  /* background: rgb(0, 255, 0); */
}

main>.mainRightContent>.Option>ul{
  width: 100%;
  margin: 0;
  padding: 0;
  list-style:none
}

main>.mainRightContent>.Option>ul>li{
  width: 100%;
}

main>.mainRightContent>.Option>ul>li>button{
  width: 30%;
}

/* main>.mainRightContent>.Option>div>button{
  width: 40%;
} */

main>.mainRightContent>.Option>input{
  width: 60%;
}

main>.mainRightContent>.Code{
  width: 100%;
  height: 55%;
  /* background: rgb(0, 0, 255); */
}

main>.mainRightContent>.Code .button{
  margin: 5%;
}

main>.mainRightContent>.Code .CodeHighlight{
  width: 100%;
  height: 90%;
}

main>.mainRightContent>.Code>.CodeHighlight .language-javascript{  
  width: 90%;
  height: 90%;
  padding: 0%;
  margin: 0%;
  /* margin: 5%; */
}

footer{
  width: 100vw;
  height: var(--fotter-height);

  position: absolute;
  top: var(--fotter-top);
  left: 0;

  background: #232323;
}

footer>p{
  margin: 0;
  color: #fff;
}

